body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#searchWrapper {
  display: grid;
  grid-template-columns: 5% 30% 5% 20%;
  grid-template-rows: 50% 50%;

  grid-column-gap: 3%;

  -webkit-column-gap: 3%;

          column-gap: 3%;
  grid-row-gap: 15%;
  row-gap: 15%;

  align-items: center;
  justify-content: center;
  padding-top: 15%;
  padding-left: 15%;
}

#query {
  border: none;
  border-radius: 30px !important;
  outline: none;

  display: inline-block;
  grid-column: 1/4;

  padding-left: 5%;

  background-color: white;
  color: black;

  font-size: 110%;

  width: 100%;
  height: 90%;
}

#buttons {
  display: grid;
  grid-column: 4;

  grid-column-gap: 5%;

  -webkit-column-gap: 5%;

          column-gap: 5%;
  grid-template-columns: 15% 50%;

  margin-left: 3.5%;
  align-items: center;
}

#searchButton {
  margin-left: 3.5%;
  grid-column: 2;
  grid-row: auto;
  background-color: white;
  border-radius: 100%;
  width: 31px;
  height: 31px;
  padding: 5px;
  transition: -webkit-transform 100ms;
  transition: transform 100ms;
  transition: transform 100ms, -webkit-transform 100ms;
}
#searchButton:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  box-shadow: 0px 0px 8px 0.001px white;
  border-radius: 100%;
  cursor: pointer;
}
#searchButton:active {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  box-shadow: 0px 0px 11px 2px red;
}

#cycleButton {
  grid-column: 1;
  grid-row: auto;
  transition: -webkit-transform 100ms;
  transition: transform 100ms;
  transition: transform 100ms, -webkit-transform 100ms;
}
#cycleButton:hover {
  box-shadow: 0px 0px 8px 0.001px white;
  border-radius: 100%;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
#cycleButton:active {
  box-shadow: 0px 0px 11px 2px red;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.dissappear {
  -webkit-animation: fade 500ms linear;
          animation: fade 500ms linear;
}
.change {
  -webkit-animation: spin 150ms linear;
          animation: spin 150ms linear;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes fade {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}
@keyframes fade {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}
#cycleButton:hover {
  cursor: pointer;
}

#searchStatus {
  width: 100%;
  bottom: 5%;
  padding-right: 0%;
  visibility: hidden;
  grid-column: 2;
  grid-row: 2;
}

.vibrate {
  -webkit-animation: shake 400ms;
          animation: shake 400ms;
}

@-webkit-keyframes shake {
  0% {
    -webkit-transform: translate(1px, 1px) rotate(0deg);
            transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    -webkit-transform: translate(-1px, -2px) rotate(-1deg);
            transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    -webkit-transform: translate(-3px, 0px) rotate(1deg);
            transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    -webkit-transform: translate(3px, 2px) rotate(0deg);
            transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(1px, -1px) rotate(1deg);
            transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    -webkit-transform: translate(-1px, 2px) rotate(-1deg);
            transform: translate(-1px, 2px) rotate(-1deg);
  }
}

@keyframes shake {
  0% {
    -webkit-transform: translate(1px, 1px) rotate(0deg);
            transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    -webkit-transform: translate(-1px, -2px) rotate(-1deg);
            transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    -webkit-transform: translate(-3px, 0px) rotate(1deg);
            transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    -webkit-transform: translate(3px, 2px) rotate(0deg);
            transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(1px, -1px) rotate(1deg);
            transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    -webkit-transform: translate(-1px, 2px) rotate(-1deg);
            transform: translate(-1px, 2px) rotate(-1deg);
  }
}

