.bookmarkHolder {
  display: grid;
  grid-template-rows: 5% 10% 75% 17%;
  height: 300px;
  width: 200px;

  background-color: black;
  color: black;

  opacity: 100%;
  float: right;
  text-align: left;

  margin-bottom: 10%;
  margin-right: 13%;

  border-radius: 8px;
}
