  color: rgb(189, 189, 189);
  background-color: transparent;

  border: none;
  border-bottom: 2px solid rgb(189, 189, 189);
  outline: none;

  width: 80%;
  opacity: 100%;

  text-align: center;
  padding-bottom: 5%;
  padding-left: 7%;

  justify-self: center;
  grid-row: 2;

  font-family: Mononten;
  font-size: 30px;
  font-weight: 1500;

  -webkit-transition: -webkit-transform 125ms linear;

  transition: -webkit-transform 125ms linear;

  transition: transform 125ms linear;

  transition: transform 125ms linear, -webkit-transform 125ms linear;
}
.bookmarkHolderName:-ms-input-placeholder {
  color: rgb(189, 189, 189);
  background-color: transparent;

  border: none;
  border-bottom: 2px solid rgb(189, 189, 189);
  outline: none;

  width: 80%;
  opacity: 100%;

  text-align: center;
  padding-bottom: 5%;
  padding-left: 7%;

  justify-self: center;
  grid-row: 2;

  font-family: Mononten;
  font-size: 30px;
  font-weight: 1500;

  -ms-transition: transform 125ms linear;

  transition: transform 125ms linear;
}
.bookmarkHolderName,
.bookmarkHolderName::placeholder {
  color: rgb(189, 189, 189);
  background-color: transparent;

  border: none;
  border-bottom: 2px solid rgb(189, 189, 189);
  outline: none;

  width: 80%;
  opacity: 100%;

  text-align: center;
  padding-bottom: 5%;
  padding-left: 7%;

  justify-self: center;
  grid-row: 2;

  font-family: Mononten;
  font-size: 30px;
  font-weight: 1500;

  transition: -webkit-transform 125ms linear;

  transition: transform 125ms linear;

  transition: transform 125ms linear, -webkit-transform 125ms linear;
}
.bookmarkHolderName:hover::-webkit-input-placeholder {
  color: white;
  border-bottom: 2px solid white;
}
.bookmarkHolderName:hover:-ms-input-placeholder {
  color: white;
  border-bottom: 2px solid white;
}
.bookmarkHolderName:hover,
.bookmarkHolderName:hover::placeholder {
  color: white;
  border-bottom: 2px solid white;
}
.bookmarkHolderName:focus {
  color: white;
  border-bottom: 2px solid white;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.addLinkButton {
  grid-row: 5;

  border: none;
  border: 2.5px solid white;
  border-radius: 100px;

  background-color: rgb(78, 76, 76);

  width: 25%;

  justify-self: center;

  transition: background-color 150ms linear, -webkit-transform 100ms linear;

  transition: transform 100ms linear, background-color 150ms linear;

  transition: transform 100ms linear, background-color 150ms linear, -webkit-transform 100ms linear;
}
.addLinkButton:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  background-color: rgb(59, 59, 59);
}
.addLinkButton:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

/* When you want to add new link Sticker */
#linkAdder {
  position: absolute;
  align-items: center;

  left: 50%;
  top: 50%;
  display: grid;

  align-items: center;
  justify-content: center;

  grid-template-rows: 50% 50%;
  grid-row-gap: 2%;
  row-gap: 2%;

  opacity: 100%;
  border-radius: 10px;

  background-color: black;

  height: 8%;
  width: 10%;

  padding: 10px;
  padding-bottom: 15px;

  visibility: hidden;
}
#linkInput,
#linkName {
  padding-left: 10px;
  padding-bottom: 3%;

  color: white;
  background-color: black;

  border: none;
  border-bottom: 2px solid white;

  width: 90%;
  transition: -webkit-transform 100ms linear;
  transition: transform 100ms linear;
  transition: transform 100ms linear, -webkit-transform 100ms linear;
}
#linkInput:focus,
#linkName:focus {
  outline: none;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
